*{
    margin: 0;
    padding: 0;
}
.app{
    text-align: center;
    color: blue;
}
.app input{
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid grey;
    outline: none;
    padding: 10px 5px;
    width: 200px;
}
button{
   background-color: blueviolet; 
   color: white;
   border: none;
   outline: none;
   margin-top: 10px;
   padding: 10px 30px;
   font-size: 16px;
   cursor: pointer;
}
nav{
    display: flex;
    background-color: rgb(8, 33, 76);
    padding: 10px 0;
    justify-content: center;
    margin-top: 5px;
}
nav ul{
    display: flex;
    list-style: none;
justify-content: space-between;
align-items: center;
width: 40%;
color: white;
font-weight: bold;cursor: pointer;
transition: all 0.5s ease-in-out;
}
nav ul li:hover{
    transform: scale(1.4);
}
.navActive{
    color: rgb(128, 0, 96);
    font-weight: bold;
    font-size: 30px;
}
.app-content{
    background-color: rgb(19, 52, 141);
    color: white;
    padding: 10px;
}
.app-content h3{
    color: white;
}